.navbar {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.navbar::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 1px;
  background-color: #979797;
  width: 100%;
  display: none;
}
.navbar .logo {
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px) saturate(100%);
  padding: 0 25px;
  z-index: 5;
}
.navbar .logo img {
  width: 40px;
  height: 40px;
}
.navbar button.toggler,
.navbar button.close {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.navbar button.toggler img {
  width: 24px;
  height: 21px;
}
.navbar button.close img {
  width: 20px;
  height: 20px;
  margin: 0 20px;
}
.navbar .navigation {
  backdrop-filter: blur(10px) saturate(100%);
  padding: 0 25px;
  background-color: rgba(255, 255, 255, 0.1);
  z-index: 1020;
}
@media (max-width: 575px) {
  .navbar .navigation {
    width: 255px;
    position: fixed;
    right: -255px;
    top: 0;
    min-height: 100vh;
    padding: 35px 0;
    transition: 0.5s;
  }
  .navbar .navigation.open {
    right: 0;
  }
}
.navbar .navigation button.close {
  width: 100%;
  text-align: right;
  margin-bottom: 40px;
}
.navbar .navigation .links li {
  padding-left: 35px;
}
.navbar .navigation .links li a {
  color: #fff;
  font-size: 16px;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 2.7px;
  text-transform: uppercase;
  padding: 15px 0;
  display: flex;
  border-right: 5px solid transparent;
  border-bottom: 3px solid transparent;
}
.navbar .navigation .links li a:hover {
  border-right-color: rgba(255, 255, 255, 0.5);
}
.navbar .navigation .links li a:active,
.navbar .navigation .links li a.active {
  border-right-color: rgba(255, 255, 255, 1);
}
.navbar .navigation .links li a .count {
  font-weight: 600;
  margin-right: 5px;
}
@media (min-width: 576px) {
  .navbar {
    padding: 20px 0;
  }
  .navbar button.toggler,
  .navbar button.close {
    display: none;
  }
  .navbar .logo {
    padding: 0 50px;
  }
  .navbar .navigation .links {
    display: flex;
    gap: 30px;
  }
  .navbar .navigation .links li {
    padding: 0;
  }
  .navbar .navigation .links li a {
    padding: 30px 0;
  }
  .navbar .navigation .links li a:hover {
    border-right-color: transparent;
    border-bottom-color: rgba(255, 255, 255, 0.5);
  }
  .navbar .navigation .links li a:active,
  .navbar .navigation .links li a.active {
    border-right-color: transparent;
    border-bottom-color: rgba(255, 255, 255, 1);
  }
  .navbar .navigation .links li a .count {
    display: none;
  }
}
@media (min-width: 992px) {
  .navbar {
    padding-right: 0;
  }
  .navbar::before {
    display: block;
  }
  .navbar .navigation {
    padding-left: 50px;
    padding-right: 90px;
  }
  .navbar .navigation .links li a .count {
    display: inline-block;
  }
}
