body.crew {
  background-image: url(../../public/assets/crew/background-crew-mobile.jpg);
}
.page-content.crew .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  gap: 30px;
}
.page-content.crew .content .image {
  border-bottom: 1px solid #3b3b3b;
  text-align: center;
  width: 100%;
}
.page-content.crew .content .image img {
  height: 300px;
}
.page-content.crew .content .text {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 30px;
  min-width: fit-content;
}
.page-content.crew .content .text .dots {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
}
.page-content.crew .content .text .dots .dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.18);
  cursor: pointer;
}
.page-content.crew .content .text .dots .dot:hover {
  background-color: #979797;
}
.page-content.crew .content .text .dots .dot.active,
.page-content.crew .content .text .dots .dot.hover {
  background-color: rgba(255, 255, 255, 1);
}
@media (min-width: 576px) {
  body.crew {
    background-image: url(../../public/assets/crew/background-crew-mobile.jpg);
  }
  .page-content.crew .content {
    justify-content: flex-end;
  }
  .page-content.crew .content .image {
    border-bottom: none;
  }
  .page-content.crew .content .image img {
    height: 50vh;
  }
  .page-content.crew .content .text {
    order: -1;
  }
  .page-content.crew .content .text .details {
    order: -1;
  }
}
@media (min-width: 992px) {
  body.crew {
    background-image: url(../../public/assets/crew/background-crew-mobile.jpg);
  }
  .page-content.crew {
    align-content: flex-end;
  }
  .page-content.crew .content {
    flex-direction: row;
  }
  .page-content.crew .content .text {
    text-align: left;
    margin-bottom: 60px;
  }
  .page-content.crew .content .text .dots {
    justify-content: flex-start;
  }
  .page-content.crew .content .text .dots .dot {
    width: 15px;
    height: 15px;
  }
  .page-content.crew .content .image img {
    max-width: 100%;
    height: auto;
  }
}
@media (min-width: 1200px) {
  .page-content.crew .content .text .details {
    margin-bottom: 50px;
  }
}
