.heading-1,
.heading-2,
.heading-3,
.heading-4,
.heading-5,
.sub-heading-1,
.sub-heading-2 {
  font-weight: 400;
  text-transform: uppercase;
  margin: 0 0 10px;
}
.heading-1 {
  font-family: "Bellefair", serif;
  font-size: 80px;
  color: #fff;
}
.heading-2 {
  font-family: "Bellefair", serif;
  font-size: 56px;
  color: #fff;
}
.heading-3 {
  font-family: "Bellefair", serif;
  font-size: 24px;
  color: #fff;
}
.heading-4 {
  font-family: "Bellefair", serif;
  font-size: 16px;
  color: rgb(255 255 255 / 50%);
}
.heading-5 {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 16px;
  letter-spacing: 2.7px;
  color: #d0d6f9;
}
.sub-heading-1 {
  font-family: "Bellefair", serif;
  font-size: 28px;
  color: #fff;
}
.sub-heading-2 {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 14px;
  letter-spacing: 2.36px;
  color: #d0d6f9;
}
.paragraph {
  color: #d0d6f9;
  font-size: 15px;
  max-width: 330px;
  line-height: 25px;
}
@media (min-width: 576px) {
  .heading-1 {
    font-size: 150px;
  }
  .heading-2 {
    font-size: 80px;
  }
  .heading-3 {
    font-size: 40px;
  }
  .heading-4 {
    font-size: 24px;
  }
  .heading-5 {
    font-size: 20px;
    letter-spacing: 3.38px;
  }
  .paragraph {
    font-size: 16px;
    max-width: 500px;
  }
}
@media (min-width: 992px) {
  .heading-2 {
    font-size: 100px;
  }
  .heading-3 {
    font-size: 56px;
  }
  .heading-4 {
    font-size: 32px;
  }
  .heading-5 {
    font-size: 28px;
    letter-spacing: 4.72px;
  }
  .paragraph {
    font-size: 18px;
  }
}
