body.home {
  background-image: url(../../public/assets/home/background-home-mobile.jpg);
}

.home .content {
  text-align: center;
}
.home .content .paragraph {
  width: 330px;
  margin: 0 auto;
}
.home button.explore-btn {
  font-family: "Bellefair", sans-serif;
  font-size: 20px;
  width: 150px;
  height: 150px;
  margin: 0 auto;
  text-transform: uppercase;
  border-radius: 50%;
  border: none;
  position: relative;
  cursor: pointer;
}
.home button.explore-btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  height: 0;
  transition: 0.3s;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  z-index: -1;
}

@media (min-width: 576px) {
  body.home {
    background-image: url(../../public/assets/home/background-home-tablet.jpg);
  }
  .home button.explore-btn {
    font-size: 32px;
    width: 230px;
    height: 230px;
  }
}
@media (min-width: 992px) {
  body.home {
    background-image: url(../../public/assets/home/background-home-desktop.jpg);
  }
  .home {
    overflow: hidden;
  }
  .home .content {
    text-align: left;
  }
  .home .content .paragraph {
    width: 450px;
  }
  .home button.explore-btn {
    letter-spacing: 2px;
    width: 250px;
    height: 250px;
    margin: initial;
  }
  .home button.explore-btn:hover::before {
    width: 400px;
    height: 400px;
  }
}
