* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Barlow", sans-serif;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}
a {
  text-decoration: none;
  display: inline-block;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.main-heading {
  width: 100%;
  margin: 30px 0 !important;
  text-align: center;
}
.main-heading .count {
  font-weight: 600;
  margin-right: 10px;
  color: rgba(255, 255, 255, 0.25);
}
.main-heading .text {
  color: #fff;
}
@media (min-width: 576px) {
  .main-heading {
    text-align: left;
  }
}
