body.technology {
  background-image: url(../../public/assets/technology/background-technology-mobile.jpg);
}
.page-content.technology .image {
  width: 100vw;
}
.page-content.technology .image img {
  width: 100%;
}
.page-content.technology .image img.portrait {
  display: none;
}
.page-content.technology .content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-start;
  padding-bottom: 60px;
}
.page-content.technology .content .text {
  display: flex;
  flex-direction: column;
}
.page-content.technology .content .text .dots {
  display: flex;
  gap: 30px;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
}
.page-content.technology .content .text .dots .dot {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: #fff;
  font-family: "Bellefair", serif;
  border: 1px solid #939393;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-content.technology .content .text .dots .dot:hover {
  border-color: #fff;
}
.page-content.technology .content .text .dots .dot.active {
  border-color: #fff;
  background-color: #fff;
  color: #000;
}
.page-content.technology .content .text .details {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.page-content.technology .content .text .details .title {
  color: #d0d6f9;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-family: "Barlow Condensed", sans-serif;
  font-size: 14px;
  letter-spacing: 2.36px;
}
.page-content.technology .content .text .details .paragraph {
  margin: 0 auto;
}
@media (min-width: 576px) {
  body.technology {
    background-image: url(../../public/assets/technology/background-technology-tablet.jpg);
  }
  .page-content.technology .content .text .dots {
    margin: 45px 0;
  }
  .page-content.technology .content .text .dots .dot {
    width: 60px;
    height: 60px;
    font-size: 24px;
    letter-spacing: 1px;
    cursor: pointer;
  }
}
@media (min-width: 992px) {
  body.technology {
    background-image: url(../../public/assets/technology/background-technology-desktop.jpg);
  }
  .page-content.technology {
    padding-right: 10px;
  }
  .page-content.technology .content {
    flex-direction: row;
    justify-content: space-between;
  }
  .page-content.technology .content .text {
    order: -1;
    flex-direction: row;
    gap: 30px;
  }
  .page-content.technology .content .text .dots {
    flex-direction: column;
  }
  .page-content.technology .content .text .details {
    text-align: left;
  }
  .page-content.technology .image {
    width: 350px;
  }
  .page-content.technology .image img.landscape {
    display: none;
  }
  .page-content.technology .image img.portrait {
    display: inline-block;
  }
}
@media (min-width: 1200px) {
  .page-content.technology .image {
    width: 400px;
  }
  .page-content.technology .content .text .dots {
    margin-right: 60px;
  }
}
@media (min-width: 1320px) {
  .page-content.technology .image {
    width: 500px;
  }
}
