body.destination {
  background-image: url(../../public/assets/destination/background-destination-mobile.jpg);
}
.page-content.destination .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
.page-content.destination .content .image img {
  width: 250px;
}
.page-content.destination .content .text {
  text-align: center;
}
.page-content.destination .content .text .navigation {
  display: flex;
  color: #fff;
  justify-content: center;
  margin-bottom: 20px;
  gap: 30px;
}
.page-content.destination .content .text .navigation li {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 14px;
  letter-spacing: 2.36px;
  text-transform: uppercase;
  border-bottom: 3px solid transparent;
  padding-bottom: 10px;
  cursor: pointer;
}
.page-content.destination .content .text .navigation li:hover {
  border-color: rgba(255, 255, 255, 0.5);
}
.page-content.destination .content .text .navigation li.active {
  border-color: rgba(255, 255, 255, 1);
}
.page-content.destination .content .text .paragraph {
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #3b3b3b;
}
.page-content.destination .content .text .details {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 30px;
}
@media (min-width: 576px) {
  body.destination {
    background-image: url(../../public/assets/destination/background-destination-tablet.jpg);
  }
  .page-content.destination .content .image img {
    width: 300px;
  }
  .page-content.destination .content .text .navigation li {
    font-size: 16px;
    letter-spacing: 2.7px;
  }
  .page-content.destination .content .text .details {
    flex-direction: row;
    gap: 100px;
  }
}
@media (min-width: 992px) {
  body.destination {
    background-image: url(../../public/assets/destination/background-destination-desktop.jpg);
  }
  .page-content.destination .content {
    flex-direction: row;
    gap: 70px;
  }
  .page-content.destination .content .text {
    text-align: left;
  }
  .page-content.destination .content .image img {
    width: 400px;
  }
  .page-content.destination .content .text .navigation {
    justify-content: flex-start;
  }
  .page-content.destination .content .text .details {
    justify-content: flex-start;
  }
}
