.page-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  padding: 0 40px;
}
@media (min-width: 992px) {
  .page-content {
    flex-direction: row;
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .page-content {
    flex-direction: row;
    padding: 0 120px;
  }
}
